import "bootstrap/dist/css/bootstrap.min.css";
import { Container, Row, Col, Button } from "react-bootstrap";
import { useParams, Navigate, useNavigate } from "react-router-dom";
import "../App.css";
import { useEffect, useState } from "react";
//addItem
import { addItem } from "../store/cartSlice";
import { useDispatch } from "react-redux";

const Detail = (props) => {
  let dispatch = useDispatch(); //  
  let Navigate = useNavigate(); //useNavigate()      .
  //    
  let [num, setNum] = useState(""); //    

  useEffect(() => {
    //1. Detail  
    let takeOut = localStorage.getItem("watched"); //2.watched  

    //3.localStorage 
    // takeOut null   ,  JSON  
    takeOut = takeOut ? JSON.parse(takeOut) : []; //JSON  

    takeOut.push(choose.id); //  
    takeOut = new Set(takeOut); //
    takeOut = Array.from(takeOut); //   array 
    localStorage.setItem("watched", JSON.stringify(takeOut)); //  watched 
  });

  useEffect(() => {
    if (isNaN(num) === true) {
      //   
      alert(" ");
    }
  });

  useEffect(() => {
    console.log("");
  });
  let [count, setCount] = useState(0);
  //App.js goods   .
  let { id } = useParams();
  console.log(id);

  //.find()   array     .
  let choose = props.goods.find(function (a) {
    return a.id == id; //array.id == url      choose 
  });

  return (
    <>
      <Container className="detailWrap">
        <Row>
          <Col sm={6}>
            {/*data.js  */}
            <img src={choose.img} width="200" alt="listImg" />
          </Col>
          <Col sm={4}>
            <h4>{choose.title}</h4> {/*  */}
            <p>{choose.content}</p> {/*  */}
            <p>{choose.price}</p> {/*  */}
            <Button
              variant="danger"
              onClick={() => {
                dispatch(
                  addItem({
                    id: choose.id,
                    img: choose.img,
                    name: choose.title,
                    price: choose.price,
                    count: 1,
                  })
                );
                Navigate("/cart");
              }}
            >
              
            </Button>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default Detail;
