import { Navigate } from "react-router-dom";

const Mypage = () => {
  const isLoggdIn = false; //     
  if(!isLoggdIn){
    return <Navigate to= "/login" replace={true} /> 
    //  false login .
  }
  return <div></div>
}

export default Mypage